}
.resetPassword-heading-line > * {
  display: inline-block;
  position: relative;
}
.resetPassword-heading-line > ::after,
.resetPassword-heading-line > ::before {
  content: "";
  position: absolute;
  top: calc(50% - ((0.2px + 0.05em) / 2));
  width: 2000px;
  border-bottom: calc(0.2px + 0.05em) solid #e5e5e5;
}
.resetPassword-heading-line > ::before {
  right: 100%;
  margin-right: calc(5px + 0.3em);
}
.resetPassword-heading-line > ::after {
  left: 100%;
  margin-left: calc(5px + 0.3em);
}
